import 'bootstrap';
import MsgReader from '@kenjiuno/msgreader';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jsoneditor/dist/jsoneditor.min.css';
import JSONEditor from 'jsoneditor';
import { decompressRTF } from '@kenjiuno/decompressrtf';
import { Buffer } from 'buffer';
import * as rxjs from 'rxjs';
{
    var _a = require('@kenjiuno/msgreader/package.json'), name_1 = _a.name, version_1 = _a.version;
    var applyReplace_1 = function (src) {
        return src
            .replace(/{package}/g, "".concat(name_1, "@").concat(version_1))
            .replace(/{build}/g, BUILT_AT);
    };
    document.title = applyReplace_1(document.title);
    document.querySelectorAll(".my-substitution-target").forEach(function (element) {
        element.textContent = applyReplace_1(element.textContent);
    });
}
var container = document.getElementById('editor_holder');
var options = {};
var editor = new JSONEditor(container, options);
function recoverCompressedRtf(msg) {
    var attachments = msg.attachments ? msg.attachments.map(function (sub) {
        var newSub = Object.assign({}, sub);
        if (newSub.innerMsgContentFields) {
            newSub.innerMsgContentFields = recoverCompressedRtf(sub.innerMsgContentFields);
        }
        return newSub;
    }) : undefined;
    var newMsg = Object.assign({}, msg);
    if (newMsg.compressedRtf !== undefined) {
        Object.defineProperty(newMsg, "rtf", {
            value: Buffer.from(decompressRTF(Array.from(newMsg.compressedRtf))).toString("ascii"),
        });
        delete newMsg.compressedRtf;
    }
    if (attachments !== undefined) {
        newMsg.attachments = attachments;
    }
    return newMsg;
}
var wip = document.getElementById('wip');
var errorPanel = document.getElementById('errorPanel');
var clearAnsiEncoding = document.getElementById('clearAnsiEncoding');
var ansiEncoding = document.getElementById("ansiEncoding");
var msgFile = document.getElementById("msgFile");
var includeRawProps = document.getElementById("includeRawProps");
clearAnsiEncoding.addEventListener('click', function () {
    ansiEncoding.value = "";
    ansiEncoding.dispatchEvent(new Event("change"));
});
rxjs.combineLatest([
    // #msgFile
    rxjs.fromEvent(msgFile, 'change')
        .pipe(rxjs.map(function (e) { return e.target.files; })),
    // #ansiEncoding
    rxjs.fromEvent(ansiEncoding, 'change')
        .pipe(rxjs.map(function (e) { return e.target.value; }), rxjs.startWith(null)),
    // #includeRawProps
    rxjs.fromEvent(includeRawProps, 'change')
        .pipe(rxjs.map(function (e) { return e.target.checked; }), rxjs.startWith(false)),
])
    .subscribe(function (set) {
    var fileList = set[0], ansiEncoding = set[1], includeRawProps = set[2];
    rxjs
        .from(fileList)
        .pipe(rxjs.take(1))
        .subscribe(function (file) {
        var reader = new FileReader();
        wip.style.display = '';
        errorPanel.style.display = 'none';
        rxjs.fromEvent(reader, 'load')
            .pipe(rxjs.take(1), rxjs.map(function (e) { return e.target.result; }), rxjs.finalize(function () { return wip.style.display = 'none'; }), rxjs.map(function (arrayBuffer) {
            var testMsg = new MsgReader(arrayBuffer);
            testMsg.parserConfig = {
                ansiEncoding: ansiEncoding,
                includeRawProps: includeRawProps,
            };
            var testMsgInfo = testMsg.getFileData();
            console.info(testMsgInfo);
            return recoverCompressedRtf(testMsgInfo);
        }))
            .subscribe({
            next: function (msgInfo) {
                editor.set(msgInfo);
            },
            error: function (ex) {
                errorPanel.style.display = '';
                errorPanel.textContent = ex + "";
            }
        });
        reader.readAsArrayBuffer(file);
    });
});
